@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-dark-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-blue: #0e52c7;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0,0,0,0.7);
  --primary-green: green;
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
}

/************************* NavBar *************************/
nav {
  z-index: 2;
}

.portfolio-logo {
  width: 3.125rem;
}

.portfolio-signature {
  width: 8.125rem;
  margin-left: 1rem;
}

/*-------------- Menu Links --------------*/
nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid transparent;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0.75rem !important;
  height: 6rem !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/************************* Header *************************/
.header-wraper {
  position: relative;
  background: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0.02)),url(/personal-website/static/media/laptop.f1693181.jpeg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.main-info h1 {
  /* margin-top: 3rem; */
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  margin-bottom: 10rem;
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }

}

.main-info h2 {
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.neon-flicker-text {
  -webkit-animation: flicker 1.5s infinite alternate;
          animation: flicker 1.5s infinite alternate;
}

.glow-text {
  text-shadow:
    0 0 4px #fff,
    0 0 11px #fff,
    0 0 19px #fff,
    0 0 40px #f9ab00,
    0 0 80px #f9ab00,
    0 0 90px #f9ab00,
    0 0 100px #f9ab00,
    0 0 150px #f9ab00;
  text-shadow:
    0 0 4px #fff,
    0 0 11px #fff,
    0 0 19px #fff,
    0 0 40px var(--primary-orange),
    0 0 80px var(--primary-orange),
    0 0 90px var(--primary-orange),
    0 0 100px var(--primary-orange),
    0 0 150px var(--primary-orange);
}

/*-------------- Typed Text --------------*/
@-webkit-keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
    text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #f9ab00,
      0 0 80px #f9ab00,
      0 0 90px #f9ab00,
      0 0 100px #f9ab00,
      0 0 150px #f9ab00;
    text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px var(--primary-orange),
      0 0 80px var(--primary-orange),
      0 0 90px var(--primary-orange),
      0 0 100px var(--primary-orange),
      0 0 150px var(--primary-orange);
  }
  20%, 24%, 55% {       
    text-shadow: none;
  }
}
@keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
    text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #f9ab00,
      0 0 80px #f9ab00,
      0 0 90px #f9ab00,
      0 0 100px #f9ab00,
      0 0 150px #f9ab00;
    text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px var(--primary-orange),
      0 0 80px var(--primary-orange),
      0 0 90px var(--primary-orange),
      0 0 100px var(--primary-orange),
      0 0 150px var(--primary-orange);
  }
  20%, 24%, 55% {       
    text-shadow: none;
  }
}

.typed-text {
  font-size: 1.75rem;
  color: #fff;
  color: var(--primary-white);
}

.typed-cursor {
  font-size: 2rem;
  color: #f9ab00;
  color: var(--primary-orange);
}

/*-------------- Header Main Offer Button --------------*/
.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/*-------------- Particles --------------*/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/************************* About Me *************************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;

}

.profile-img {
  width: 20rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/************************* Education *************************/
.education {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.education h1 {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.education-img-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  height: 6rem;
}

.education-img {
  width: 8rem;
  padding: .3rem;
}

.education h3 {
  height: 3.5rem;
}

.education h6 {
  font-weight: 700 !important;
}

.education .box {
  height: 23rem;
  border-bottom: 0.3125rem solid #f9ab00;
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: 0.625rem 0.3rem;
  margin-bottom: 1.875rem;
  background: #fff;
  background: var(--primary-white);
  position: relative;
  transition: .3s ease-in-out;
}

.education-date {
  color: #f9ab00;
  color: var(--primary-orange);
}

.gpa {
  color: #0e52c7;
  color: var(--primary-blue);
}

.education .box:hover {
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid rgb(52, 58, 64);
  border-bottom: 0.3125rem solid var(--primary-dark);
}


.education .box:hover h6 {
  color: #fff;
  color: var(--primary-white);
}

.education .box:hover .major {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
}


@media(max-width: 1000px) {
  .education .box {
    height: 26rem;
  }
}

/************************* Experience *************************/
.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

.logo-wrap {
  display: flex;
  justify-content: start;
  align-items: start;
  margin-top: 1rem;
  margin-bottom: 1rem;
  height: 2rem;
}

.logo-img {
  width: 2rem;
  height: auto;
  margin-right: 0.5rem;
}

.logo-text {
  text-transform: uppercase;
  display:flex;
  align-items: center;
  height: 100%;
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52, 58, 64);
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #666666;
  color: var(--primary-dark-grey);
  direction: ltr
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #0e52c7;
  color: var(--primary-blue);
  font-weight: 900;;
}

.timeline-content .position {
  color: #f9ab00;
  color: var(--primary-dark-orange);
}

.timeline-content ul {
  border-left: 0.125rem solid #f9ab00;
  border-left: 0.125rem solid var(--primary-orange);
}

/*-------------- Media --------------*/
@media(max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }
  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

/************************* Project *************************/
.project {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.project h1 {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.project-img-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  height: 8rem;
}

.project-img {
  width: 80%;
  height: 100%;
}

.project .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.project .icon {
  font-size: 1.4rem;
  color: #fff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: .3s ease-in-out;
}

.project h3 {
  height: 3.5rem;
}

.project .box {
  height: 30rem;
  border-bottom: 0.3125rem solid #f9ab00;
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  background: var(--primary-white);
  position: relative;
  transition: .3s ease-in-out;
}

.project .learn-more {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin-top: 2rem;
  margin-bottom: 0.8rem;
}

.project .box:hover {
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid rgb(52, 58, 64);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.project .box:hover .icon {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
}

.project .box:hover .circle {
  background: #fff;
  background: var(--primary-white);
}

.project .box:hover p {
  color: #fff;
  color: var(--primary-white);
}

.project .learn-more:hover {
  color: #fff;
  color: var(--primary-white)
}

@media(max-width: 1000px) {
  .project .box {
    height: 33rem;
  }
}

/************************* Portfolio *************************/

.portfolio-wrapper {
  text-align: center;
  padding: 3rem 0;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.portfolio-wrapper h1 {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.portfolio-image-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  position: relative;
}

.portfolio-image {
  width: 15rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}

/*-------------- Overflow --------------*/
.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

/*-------------- Overflow Box --------------*/
.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);
}

.hyper-link:hover {
  color: #0e52c7;
  color: var(--primary-blue);
}

.portfolio-popupbox-ul {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  text-align: start;
  border-left: 2px solid #f9ab00;
  border-left: 2px solid var(--primary-orange);
}

/*-------------- Media --------------*/
@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 500px) {
  .popupbox-wrapper {
    height: 100%;
  }
  .portfolio-image-popupbox {
    width: 60%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }
  .portfolio-image-popupbox {
    width: 30%;
  }
}

/************************* Testimonials *************************/


/************************* Contacts *************************/
.contacts {
  background: rgb(52, 58, 64);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/*-------------- Inputs --------------*/
.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.1rem;
}

.contacts-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #ef4035;
  color: var(--primary-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}

/*-------------- Media --------------*/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  .contacts .container {
    padding: 0 1rem;
  }
  .contacts p {
    padding: 0 0.4rem;
  }
}

/************************* Footer *************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}
